@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    local("Montserrat"),
    local("Montserrat Regular"),
    url('../fonts/montserrat-v18-latin_cyrillic-regular.woff2') format('woff2'),
    url('../fonts/montserrat-v18-latin_cyrillic-regular.woff') format('woff');
}

